import { makeObservable, action } from 'mobx';

class ValidationStore {
    maxRegularInputLength = 200;
    maxHoursServiceDurationInputValue = 10;
    maxMinutesServiceDurationInputValue = 60;
    maxServicePriceInputLength = 10;
    maxServiceDescriptionInputLenght = 1000;

    constructor() {
        makeObservable(this, {            
            handleReguralInputChange: action
        }, {deep: true, });
    }

    handleReguralInputChange = (e, setState) => {
        const value = e.target.value;
    
        //       ,  
        if (value.length > this.maxRegularInputLength) {
            setState(value.slice(0, this.maxRegularInputLength));
        } else {
            setState(value);
        }
    };

    handleServiceDescriptionInputChange = (e, setState) => {
        const value = e.target.value;
    
        //       ,  
        if (value.length > this.maxServiceDescriptionInputLenght) {
            setState(value.slice(0, this.maxServiceDescriptionInputLenght));
        } else {
            setState(value);
        }
    };
}

const validationStore = new ValidationStore();

export default validationStore;